import React from "react";

import { useAppDispatch, useAppSelector } from "../../App/hooks";

import { LevelList } from "./LevelList";
import {
  activityPathContinued,
  damageCreated,
  setNewActivityParent,
} from "../RoutineSlice/routineSlice";
import {
  activityTypeOptions,
  profTrendOptions,
  conditionOptions,
  defenseOptions,
  MAPOptions,
  rollOptions,
  statTrendOptions,
  itemBTrendOptions,
} from "../../Model/options";
import {
  activityPathRemoved,
  activityPathUpdated,
  selectactivityPathById,
} from "../RoutineSlice/routineSlice";
import { activityTypes } from "../../Model/types";
import { Damage } from "./Damage";
import { Effect } from "./Effect";
import { effectCreated } from "../RoutineSlice/routineSlice";
import { RootState } from "../../App/store";
import {
  Button,
  FormControl,
  Grid,
  InputLabel,
  List,
  ListSubheader,
  Paper,
  Select,
  Tooltip,
  Typography,
} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import { ActivityPathStub } from "../ActivityPathStub";
import { TooltipSelect } from "../../TooltipSelect";

export const ActivityPath = ({ id }: { id: number }) => {
  const {
    parentId,
    routineId,
    condition,

    rollType,
    type,
    profTrend,
    statTrend,
    itemTrend,
    bonusAdjustments,
    MAP,
    targetType,

    damages,
    effects,
    apIds,
  } = useAppSelector((state: RootState) => selectactivityPathById(state, id)!);
  const dispatch = useAppDispatch();

  const bonusLevelList = LevelList(
    "bonusAdjustments",
    dispatch,
    activityPathUpdated,
    id,
    bonusAdjustments
  );

  return (
    <Paper sx={{ my: 2, p: 1 }}>
      <Grid container spacing={{ xs: 1, sm: 2 }} sx={{ my: 0 }}>
        <Grid item xs="auto">
          <Button
            variant="outlined"
            size="small"
            startIcon={<DeleteIcon />}
            onClick={(e) => {
              dispatch(activityPathRemoved({ id, parentId, routineId }));
            }}
          >
            Delete
          </Button>
        </Grid>
        {parentId !== undefined ? (
          <Grid item xs>
            <TooltipSelect
              title='Depending on the roll of the parent activity, should this activity happen. For example: to add another strike only when you hit with the previous one, have the second strike be a child of the first with the condition "Success or Better"'
              value={condition}
              label="Condition"
              onChange={(e) =>
                dispatch(
                  activityPathUpdated({
                    id,
                    changes: { condition: e.target.value },
                  })
                )
              }
            >
              {conditionOptions}
            </TooltipSelect>
          </Grid>
        ) : (
          ""
        )}
      </Grid>
      <Typography variant="h6" gutterBottom>
        Roll
      </Typography>
      <Grid
        container
        spacing={{ xs: 1, sm: 2 }}
        sx={{ my: 2, "&:hover": { bgcolor: "rgb(50,50,50,.2)" } }}
      >
        <Grid item>
          <TooltipSelect
            title="Advantage is rolling twice and taking the higher roll, disadvantage is taking the lower"
            label="Roll Type"
            value={rollType}
            onChange={(e) => {
              dispatch(
                activityPathUpdated({
                  id,
                  changes: { rollType: e.target.value },
                })
              );
            }}
          >
            {rollOptions}
          </TooltipSelect>
        </Grid>
        <Grid item>
          <FormControl size="small">
            <InputLabel id="activity-type-input">Activity Type</InputLabel>
            <Select
              labelId="activity-type-input"
              label="Activity Type"
              value={type}
              onChange={(e) => {
                dispatch(
                  activityPathUpdated({
                    id,
                    changes: { type: e.target.value },
                  })
                );
              }}
            >
              {activityTypeOptions}
            </Select>
          </FormControl>
        </Grid>
        <Grid item>
          <TooltipSelect
            title="An increase in proficiency (+2) is applied at each level in ()"
            label="Proficiency"
            value={profTrend}
            onChange={(e) => {
              dispatch(
                activityPathUpdated({
                  id,
                  changes: { profTrend: e.target.value },
                })
              );
            }}
          >
            {profTrendOptions}
          </TooltipSelect>
        </Grid>
        <Grid item>
          <TooltipSelect
            title="Bonuses from the listed ability score are applied at the appropriate levels. An ability score starts at the first value and is increased at levels 5, 10, 15, and 20 or until it reaches the second value listed. 'apex' means a bonus from an apex item is added to that score at the level in ()"
            label="Ability Score"
            value={statTrend}
            onChange={(e) => {
              dispatch(
                activityPathUpdated({
                  id,
                  changes: { statTrend: e.target.value },
                })
              );
            }}
          >
            {statTrendOptions}
          </TooltipSelect>
        </Grid>
        <Grid item>
          <TooltipSelect
            title="Item bonuses are applied at the levels in ()."
            label="Item Bonus"
            value={itemTrend}
            onChange={(e) => {
              dispatch(
                activityPathUpdated({
                  id,
                  changes: { itemTrend: e.target.value },
                })
              );
            }}
          >
            {itemBTrendOptions}
          </TooltipSelect>
        </Grid>
        {bonusLevelList}
        {type !== activityTypes.SAVE ? (
          <Grid item>
            <TooltipSelect
              title="The multiple attack penalty that is applied to this attack."
              label="MAP"
              value={MAP}
              onChange={(e) => {
                dispatch(
                  activityPathUpdated({
                    id,
                    changes: { MAP: e.target.value },
                  })
                );
              }}
            >
              {MAPOptions}
            </TooltipSelect>
          </Grid>
        ) : (
          ""
        )}
        <Grid item>
          <TooltipSelect
            title="What defense is targeted"
            label="Target Defense"
            value={targetType}
            onChange={(e) => {
              dispatch(
                activityPathUpdated({
                  id,
                  changes: { targetType: e.target.value },
                })
              );
            }}
          >
            {defenseOptions}
          </TooltipSelect>
        </Grid>
      </Grid>

      {/* <List subheader={<ListSubheader>Damages</ListSubheader>}> */}
      <Typography variant="h6" gutterBottom>
        Damages
      </Typography>
      {damages.map((damageId) => (
        <Damage parentId={id} id={damageId} key={damageId} />
      ))}
      {/* </List> */}
      <Button
        sx={{ mb: 3 }}
        variant="outlined"
        size="small"
        onClick={() => dispatch(damageCreated({ parentId: id }))}
      >
        Add Damage
      </Button>
      <Typography variant="h6" gutterBottom>
        Effects
      </Typography>
      {effects.map((effectId) => (
        <Effect parentId={id} id={effectId} key={effectId} />
      ))}
      <Button
        sx={{ mb: 3 }}
        variant="outlined"
        size="small"
        onClick={() => dispatch(effectCreated({ parentId: id }))}
      >
        Add Effect
      </Button>

      <List subheader={<ListSubheader>Child Activities</ListSubheader>}>
        {apIds.map((apId) => (
          <ActivityPathStub
            key={apId}
            id={apId}
            level={0}
            displayChildren={false}
          />
        ))}
      </List>

      {type === activityTypes.STRIKE ? (
        <Button
          variant="contained"
          onClick={() => {
            window.location.href = "#routine-activity-list";
            dispatch(
              activityPathContinued({
                parentId: id,
              })
            );
          }}
        >
          Continue Attack
        </Button>
      ) : (
        ""
      )}
      <Button
        variant="outlined"
        onClick={(event) => {
          window.location.href = "#routine-activity-list";
          dispatch(setNewActivityParent({ parentId: id }));
        }}
      >
        New Child Activity
      </Button>

      {/* <div className="box">
        {apIds.map((apId) => (
          <ActivityPath id={apId} key={apId} />
        ))}
        <button
          className="add"
          onClick={() =>
            dispatch(
              activityPathCreated({
                parentId: id,
                isStrike: true,
                applyMAP: true,
              })
            )
          }
        >
          + Strike
        </button>
        <button
          className="add"
          onClick={() =>
            dispatch(activityPathCreated({ parentId: id, isStrike: false }))
          }
        >
          + Save
        </button>
      </div> */}
    </Paper>
  );
};
